/* Common */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

#about-me, #skills, #works {
  margin: 70px 0;
  border-top: 1px #aaa solid;
}

#about-me h2, #skills h2,#works h2 {
  text-align: center;
}


/* Header */

header {
  display: flex;
  justify-content: center;
  padding: 20px;
  background: #aaa;
}

#logo {
  border: 2px solid white;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  background-color: transparent;
  color: white;
  font-family: 'Comic Sans MS', 'Chalkboard SE', 'Comic Neue', sans-serif; 
  font-size: 1.5em;
  transition: all .3s;
}

#logo:hover {
  color: black;
  border: 2px solid black;

}

#logo p {
  margin: 0;
}


#hamburger {
  cursor: pointer;
  font-size: 2.5em; /* Change hamburger size */
  color: white;
  /* Your hamburger styles here */
}

.navbar {
  position: fixed;
  right: 0;
  top: 0;
  width: 100%; /* Change navbar to full width */
  height: 100%;
  background: black;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform 0.5s ease-in-out;
  transform: translateX(100%); /* Initially hide the navbar */
}

.navbar.open {
  transform: translateX(0); /* Show the navbar when open */
}

.navbar ul {
  list-style: none;
  padding: 0;
}

.navbar ul li {
  margin-bottom: 1em; /* Increase space between links */
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  font-size: 3em; /* Increase link size */
}

.close {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  font-size: 2em; /* Increase close button size */
}

/* Main */

main {
  padding: 20px;
}

/* Hero */

#hero {
  width: 100%;
  max-width: 500px; /* Set a maximum width for the hero image */
  margin: 0 auto; /* Center the hero image if its width is less than the viewport width */
}

#hero img {
  width: 80%;
  height: auto;
  margin: 40px auto;
  display: block;
}

/* About Me */
#about-me ul {
  padding-left: 0; /* Reset padding */
}


#about-me li{
  width: 80%;
  max-width: 800px;
  padding: 16px;
  margin: 20px auto;
  list-style: none;
  border: #aaa 3px solid;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  box-sizing: border-box; /* add this line */
}



#about-me h3 {
  margin-top: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Skills */
#skills ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* メイン軸（横方向）でのアイテムの配置を中央に */
  align-items: center; /* 交差軸（縦方向）でのアイテムの配置を中央に */
}

#skills li {
  width: 50%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 5px;
  display: flex; /* li要素をFlexboxコンテナに */
  justify-content: center; /* li内のコンテンツを中央寄せに */
  align-items: center; /* 縦方向も中央寄せに */
}



/* Works */
#works ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  padding: 0;
}

#works ul li {
  width: calc(50% - 100px); /* Control the width of li and subtracting margins */
  margin: 30px; /* Set margins to create space between li elements */
  overflow: hidden; /* Ensures the image stays within the boundaries of the li */
  aspect-ratio: 1 / 1; /* CSS property sets the aspect ratio of the box */
}

#works ul li img {
  width: 350px;
  height: 350px;
  object-fit: cover;
  transition: transform 0.3s ease;
  margin: 0 auto;
  display: block;
}

@media (max-width: 767px) { /* Rules for mobile */
  #works ul li {
    width: 100%; /* Set the width you want for mobile here */
    margin: 5% auto; /* Set the margins you want for mobile here */
  }
}

#works ul li p {
  text-align: center;
}


#works ul li img:hover {
  transform: scale(1.05);
}

#works ul li p {
  text-align: center; /* Center the caption */
}


/* Footer */
footer {
  padding: 20px;
  text-align: center;
  color: white;
  background: #aaa;
}

