/* AIProject.css */

.netkeiba {
    font-weight: bold;
    margin: 0;
    border-bottom: 1px solid #000;
}
.netkeiba :hover{
  color: #555;
}

.race-results {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* Responsive columns */
    grid-auto-rows: auto;
    gap: 1.5rem;
    max-width: 1200px; /* Set a maximum width */
    margin: 0 auto; /* Center the grid if its width is less than the viewport width */
  }
  
  @media (min-width: 767px) {
    .race-results {
      grid-template-columns: repeat(2, 1fr); /* It will be 2 columns for screens wider than 600px */
    }
  }
  
  .race-results > div {
    overflow-wrap: break-word; /* Prevents text from overflowing its container */
    word-wrap: break-word; /* For IE */
  }
    
  .race-results li {
    list-style: none;
  }
    
  .race {
    border-radius: 16px;
    border: #555 1px solid;
    display: grid;
    grid-template-rows: auto 1fr;
    gap: 1rem;
    padding: 0.5rem;
    background-color: #F0F0F0; /* 背景色を灰色に */
  }
    
  .race h4 {
    grid-row: 1;
    text-align: center;
    margin: 10px 0;
    font-size: 24px;
  }
    
  .race-model {
    grid-row: 1;
    text-align: center;
    font-size: 0.8em;
    margin: 0;
    color: #555;
    font-size: 14px;
    font-weight: bold;
  }
  
  .results {
    grid-row: 2;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0.1rem;
  }

  .results-title {
   font-size: 16px;
  }
  
  .prediction, .actual {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .prediction h5, .actual h5 {
    margin-bottom: 0.5rem;
  }
  
  .correct-horse {
    font-weight: bold;
    color: blue;
  }
  
  .correct-order {
    color: red;
  }
  
  .place {
    color: black;
  }